.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(rgba(91, 61, 44, 0.92), rgba(91, 61, 44, 0.92)),
    url('../../assets/header_img.jpeg');
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  background-color: #412222;
  overflow: hidden;
  position: relative;
}

/* Balloons Floating Animation */
.balloon {
  width: 50px;
  height: 70px;
  background: radial-gradient(circle, #ff7eb3, #ff4b9a);
  border-radius: 50%;
  position: absolute;
  bottom: -100px;
  animation: floatUp 8s linear infinite;
}

@keyframes floatUp {
  0% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
  50% {
    transform: translateY(-50vh) scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: translateY(-100vh) scale(1.1);
    opacity: 0;
  }
}

/* Header Centering */
.separatorContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Name Styling */
.textName {
  color: #ffffff;
  font-family: 'Kaushan Script', cursive;
  font-size: 120px;
  text-align: center;
}

/* Invitation Text */
.invitationText {
  margin-top: 50px;
  color: #ffffff;
  font-family: Poppins;
  font-weight: 500;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
}

.invitationTo {
  font-family: 'Kaushan Script', cursive;
  font-size: 40px;
}

/* Button Container */
.buttonContainer {
  margin-top: 50px;
}

/* Button Animation */
.buttonContainer button {
  font-size: 20px;
  padding: 12px 24px;
  border-radius: 8px;
  background: #ff7eb3;
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.buttonContainer button:hover {
  background: #ff4b9a;
  transform: scale(1.1);
}

/* Mobile Responsive */
@media only screen and (max-width: 480px) {
  .container {
    height: 80vh;
    background: linear-gradient(rgba(91, 61, 44, 0.92), rgba(91, 61, 44, 0.92)),
      url('../../assets/header_img_mobile.png');
    background-repeat: no-repeat;
  }

  .textName {
    font-size: 50px;
  }

  .invitationText {
    font-size: 12px;
    line-height: 30px;
  }

  .buttonContainer {
    margin-top: 30px;
  }
}
